import { configureStore } from "@reduxjs/toolkit";
import { combineReducers } from "redux";
import chatWidgetReducer from "../components/chat-widget/chat-widget-slice";
import storage from "redux-persist/lib/storage";
import { persistReducer, persistStore } from "redux-persist";
import thunk from "redux-thunk";

// TODO: Set in localStorage and recall on load

// https://blog.logrocket.com/persist-state-redux-persist-redux-toolkit-react/

const persistConfig = {
  key: "root",
  storage
};

const persistedReducer = persistReducer(persistConfig, chatWidgetReducer);

export const store = configureStore({
  reducer: { chatWidget: persistedReducer },
  middleware: [thunk]
});

export const persistor = persistStore(store);
