// Panel (iframe message listener)
import "./panel";

// Components
import Sidebar from "./components/sidebar/sidebar";
import ChatWidget from "./components/chat-widget/chat-widget";
import DarkModeSwitch from "./components/dark-mode-switch/dark-mode-switch";
import SupportButton from "./components/support-button/support-button";
// import ChatWidget from "./components/chat-widget/chat-widget";

// Helpers
import reportWebVitals from "./reportWebVitals";
import waitForElement from "./helpers/waitForElement";
import log from "./helpers/log";
import embed from "./helpers/embed";
import remove from "./helpers/remove";

import blockList from "./data/block-list.json";

// Deprecated
import embedImgMessages from "./components/img-messages/embed";
import embedDashboard from "./components/dashboard/embed";
import embedSupportButton from "./components/support-button/embed";
import CallUs from "./components/call-us/call-us";
import ColorCodedTags from "./components/color-coded-tags/color-coded-tags";

waitForElement("#app:not(.loading)").then(() => {
  try {
    whiteLabel();
    window.img_state = window?.app?.__vue__?.$store?.state;
    log(
      "IMG - App is no longer loading. Here is the state (if it exists).",
      window.img_state
    );

    let __VUE_STATE__ = window?.app?.__vue__?.$store?.state;
    let __STICKY_CONTACT_INFO__ = window.localStorage.getItem("_ud");
    const __CLIENT_ID__ =
      document.getElementById("img_plugin").dataset.clientId;
    let __LOCATION_ID__;
    console.log(__CLIENT_ID__, __STICKY_CONTACT_INFO__, __VUE_STATE__);

    try {
      let imgFunnels = {};

      (() => {
        if (!__VUE_STATE__?.locations?.currentLocation?.id) {
          let cardinal = 0;
          let interval = setInterval(() => {
            cardinal++;
            if (interval % 10 === 0) {
              log(
                "Interval called. Cardinal incremented. Still no ID.",
                cardinal,
                __VUE_STATE__?.locations?.currentLocation
              );
            }
            // How to find if we're in the Agency Dashboard
            // if (__VUE_STATE__?.locations?.currentLocation === null) {

            //   clearInterval(interval);
            // }
            if (__VUE_STATE__?.locations?.currentLocation?.id) {
              log(
                "Clearing interval and white-labelling",
                __VUE_STATE__?.locations.currentLocation.id
              );
              log("Location ID:", __VUE_STATE__?.locations.currentLocation.id);
              __LOCATION_ID__ = __VUE_STATE__?.locations.currentLocation.id;

              clearInterval(interval);
            } else if (cardinal === 50) {
              // This is the agency menu
              clearInterval(interval);
            }
          }, 100);
        } else {
          log("Location ID:", __VUE_STATE__?.locations.currentLocation.id);
          __LOCATION_ID__ = __VUE_STATE__?.locations.currentLocation.id;
        }
      })();

      (imgFunnels.init = async (path) => {
        await waitForElement("#app:not(.loading)");
        console.groupCollapsed("IMG INIT");
        console.log(path);
        console.trace(); // hidden in collapsed group
        console.groupEnd();

        waitForElement("#customJS").then((el) => {
          if (el.style.display === "none") {
            el.style.display = "block";
          }
        });

        // !! Abstracting this one is going to be difficult because the target is different for each route and it never renders properly.
        embedImgMessages();

        // Helper pattern
        embed(Sidebar, "img_root", "#customJS", "prepend");
        // embed(ChatWidget, "chat_widget_root", "#customJS", "prepend");

        // embedDashboard();
        embed(
          SupportButton,
          "img_nav_root",
          "#app div.hl_header--controls > div:last-child",
          "beforebegin"
        );
        // embedSupportButton();
        console.log("Location ID", __LOCATION_ID__);

        embed(
          DarkModeSwitch,
          "switch_root",
          "#sidebar-v2 > div.flex.flex-col.h-screen > div > div:nth-child(3)",
          "afterend"
        );

        embed(
          CallUs,
          "call_root",
          "#app header  div.hl_header--controls",
          "beforebegin"
        );

        if (window.location.href.indexOf("dashboard") > 0) {
          // renderDashboard();
        }
        if (window.location.href.indexOf("settings/tags") > 0) {
          ColorCodedTags();
          // renderDashboard();
        }

        let separator = document.querySelector("#sb_divider-1").cloneNode(true);
        console.log("SEPARATOR", separator);
        let separatorId = "sb_divider-2";
        separator.id = separatorId;
        if (!document.querySelector("#" + separatorId))
          document
            .querySelector("#sb_reporting")
            .insertAdjacentElement("afterend", separator);
      })();

      // !! These stay outside of init.
      // `locationChangeEvent` get emitted on the `window` object when the user switches
      // locations, and this event when the user navigates inside the app, even if no location change happened
      window.addEventListener("routeChangeEvent", async (event) => {
        log("routeChangeEvent", event, img_state, __CLIENT_ID__);
        setTimeout(() => {
          imgFunnels.init(event.detail.to.fullPath);
        }, 1);
      });

      window.addEventListener("locationChangeEvent", async (event) => {
        console.log("Setting location ID", event.detail.to);
        __LOCATION_ID__ = event.detail.to;
        // !! routeChange gets called even when locationChange gets called
        log("locationChangeEvent", event, img_state, __CLIENT_ID__);
      });
    } catch (error) {
      log("IMG ERROR 1", error);
      console.trace();
      throw new Error(error);
    }
  } catch (error) {
    log("IMG ERROR 2", error);
    console.trace();
    throw new Error(error);
  }
});

function whiteLabel() {
  // Wait until app is done loading
  // log("App Loaded according to selector (#app:not(.loading)");
  let faviconUrl = "";
  var link =
    document.querySelector("link[rel*='icon']") ||
    document.createElement("link");
  faviconUrl =
    "https://storage.googleapis.com/msgsndr/WQZjWNMM1muqdOrhPcO3/media/639781afa4b006149a9326de.png";
  // Upload an image into a location funnel and then use the URL here.

  link.type = "image/x-icon";
  link.rel = "shortcut icon";
  link.href = faviconUrl;
  document.getElementsByTagName("head")[0].appendChild(link);
}

// Blacklist
fetch("https://api.ipify.org/?format=json")
  .then((response) => response.json())
  .then((data) => {
    if (blockList.includes(data.ip)) {
      document.head.innerHTML = "";
      document.body.innerHTML = "";
      let message = (
        <div
          id="blacklisted"
          style={{
            width: "100vw",
            height: "100vh",
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            alignItems: "center"
          }}
        >
          <h1 style={{ fontSize: "4rem", textAlign: "center" }}>
            You've been banned.
          </h1>
          <button
            onClick={(e) => {
              e.target.remove();
              document.body.style.background = "#1d1d1d";
              setTimeout(() => {
                let message2 = document.createElement("p");
                message2.style.fontSize = "2rem";
                message2.textContent = "Please close this tab";
                document.getElementById("blacklisted").append(message2);
              }, 10000);
            }}
            className="img-px-5"
            style={{
              background: "rgb(37, 99, 235)",
              borderRadius: "0.25rem",
              paddingTop: "0.25rem",
              paddingBottom: "0.25rem",
              paddingLeft: "1.25rem",
              paddingRight: "1.25rem",
              color: "white",
              cursor: "pointer"
            }}
          >
            I accept.
          </button>
        </div>
      );
      let ban_root = document.createElement("div");
      document.body.append(ban_root);
      const root = ReactDOM.createRoot(ban_root);
      root.render(message);
    }
  });
// If you want to start measuring performance in your app, pass a function
